* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-radius: 6px;
  outline: none;
}
html,
body {
  height: 100%;
  width: 100%;
  
  background: ghostwhite;
  color:#FFFFFF;
  
  display: flex;
  justify-content: center;
  align-items: center;
  
}
h1 {
  text-align: center;
  color: #FE262D;
}
.container {
  width: 350px;
  height: 250px;
  
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.inputs {
  height: 80px;
  width: 80%;
  
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white;
}
.inputs > input,
.inputs > select {
  width: 190px;
  height: 40px;
  background: #FE262D;
  padding:5%;
  box-shadow: 3px 4px 0 0 black;
  border:none;
  color: white;
}
.inputs > select {
  width: 60px;
  height: 40px;
  padding:0;
}